import { stringToId } from "./misc.js";
import { oracleManagerFactory } from "../contract/factory/rest.js";
import { DEBUG } from "../../shared/config/constant.js";
import { normalizeOptionSymbol, getNormalizedOptionSymbols } from "./config.js";
import { isProdChain } from "../../v2_lite_dpmm/utils.js";

export const PRESERVED_SYMBOLS = ["BTCUSD", "ETHUSD", "BNBUSD", "NULSUSDT", "ONTUSD"];

export const getOracleServerUrl = (chainId, symbol, type = "future", version="") => {
  //if (/^[0-9]+$/.test(symbolId.toString())) {
  let method = version === "v3" ? "get_signed_price_v3" : "get_signed_price" 
  if (type === "option") {
    if (version === "v3") {
      method = "get_signed_volatility_v3";
    } else {
      method = "get_signed_volatility";
    }
  }
  if (version !== 'v3' && symbol && PRESERVED_SYMBOLS.includes(symbol.toUpperCase())) {
    method = "get_price";
    symbol = `${symbol}_v2_bsc`;
  }
  let baseUrl = isProdChain(chainId)
    ? `https://oracle4.deri.finance/${method}`
    : `https://oracle2.deri.finance/${method}`;
  const addSymbolParam = (url, symbol) => `${url}?symbol=${symbol}`;

  if (symbol) {
    return addSymbolParam(baseUrl, symbol);
  } else {
    return baseUrl;
  }
};

export const getPriceInfoFromRest = async (chainId, symbol, type='future', version='') => {
  let url = getOracleServerUrl(chainId, symbol, type, version);
  let retry = 3;
  DEBUG && console.log('symbol oracle url: ', url)
  let res, priceInfo;
  while (retry > 0) {
    try {
      res = await fetch(url, {
        mode: "cors",
        cache: "no-cache",
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        //body: JSON.stringify(symbolList),
      });
      if (res.ok) {
        priceInfo = await res.json();
        if (priceInfo.status.toString() === "200" && priceInfo.data) {
          console.log(symbol, priceInfo.data)
          return priceInfo.data;
        }
      }
    } catch (err) {
      DEBUG && console.log(err)
    }
    retry -= 1;
  }
  if (retry === 0) {
    throw new Error(
      `getPriceInfoFromRest exceed max retry(3): ${symbol}`
    );
  }
};

export const getSymbolPrice = async (chainId, oracleManagerAddress, symbol, version) => {
  if (oracleManagerAddress !== '') {
  const id = symbol.length === 66 ? symbol : stringToId(symbol)
  const oracleManager = oracleManagerFactory(chainId, oracleManagerAddress);
  return await oracleManager.getValue(id);
  } else {
    const priceInfo = await getPriceInfoFromRest(chainId, symbol, version);
    return priceInfo.price
  }
};

export const getSymbolVolatility = async (chainId, symbol, version = "") => {
  const info = await getPriceInfoFromRest(
    chainId,
    symbol,
    "option",
    version
  );
  if (info.volatility) {
    return info.volatility;
  }
  throw new Error('invalid data type: symbol volatility is empty')
};

export const getSymbolVolatilitiesForLens = async (
  chainId,
  symbols = [],
  version = ""
) => {
  const volatilities = await Promise.all(
    symbols.reduce(
      (acc, s) => [...acc, getSymbolVolatility(chainId, `VOL-${s}`, version)],
      []
    )
  );
  return symbols.map((s, index) => [s, "0", volatilities[index]]);
};

export const getSymbolVolatilityInfo = async (chainId, symbol, version = "") => {
  const info = await getPriceInfoFromRest(
    chainId,
    symbol,
    "option",
    version
  );
  if (info.volatility) {
    return info;
  }
  throw new Error('invalid data type: symbol volatility is empty')
};


export const getSignedVolatilities = async (chainId, symbols = []) => {
  let volatilities = [];
  let oracleSymbols = [];
  const volatilityInfos = await Promise.all(
    getNormalizedOptionSymbols(symbols).reduce(
      (acc, s) => [...acc, getSymbolVolatilityInfo(chainId, `VOL-${s}`, "v3")],
      []
    )
  );
  volatilities = Object.values(symbols).reduce((acc, s , index) => {
    const oracleSymbol = normalizeOptionSymbol(s);
    const p = volatilityInfos.find((vol) => vol.symbolName === `VOL-${oracleSymbol}`)
    if (!oracleSymbols.includes(oracleSymbol) && p && oracleSymbols.length < volatilityInfos.length) {
      oracleSymbols.push(oracleSymbol);
      acc.push([
        stringToId(`VOL-${oracleSymbol}`),
        p.timestamp,
        p.volatility,
        parseInt(p.v).toString(),
        p.r,
        p.s,
      ]);
    }
    return acc;
  }, []);
  return volatilities;
};

export const getSignedPrices = async (chainId, symbols = []) => {
  let prices = [];
  if (symbols.length > 0) {
    const priceInfos = await Promise.all(
      symbols.reduce((acc, s) => [
        ...acc,
        getPriceInfoFromRest(chainId, s, "future", "v3"),
      ], [])
    );
    console.log('-',priceInfos)
    prices = Object.values(priceInfos).reduce((acc, p, index) => {
      console.log('--',symbols[index])
      acc.push([
        stringToId(symbols[index]),
        p.timestamp,
        p.price,
        parseInt(p.v).toString(),
        p.r,
        p.s,
      ]);
      return acc;
    }, []);
  }
  return prices;
};