import {
  checkApiInput,
  checkApiInputWithoutAccount,
  checkTokenId,
  checkAmount,
} from '../../shared/config';
import { catchApiError } from '../../shared/utils/api';
import { poolApiFactory } from '../contract/PoolApi';
import { ERC20Factory } from '../../shared/contract/factory';
//import { calculateDpmmCost } from '../../v2_lite_dpmm/calc';
import { bg } from '../../shared/utils';
import { calculateDpmmCost } from '../utils/futures';
import { checkToken, getBTokenAddress, getSymbolInfo } from '../config';

export const getUserStakeInfo = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  return catchApiError(
    async () => {
      [chainId, poolAddress, accountAddress] = checkApiInput(
        chainId,
        poolAddress,
        accountAddress
      );
      const api = poolApiFactory(chainId, poolAddress);
      await api.init();
      return await api.getUserStakeInfo(accountAddress);
    },
    [],
    {
      chainId: chainId,
      poolAddress: poolAddress,
      isStaked: false,
      bTokens: []
    }
  );
};

export const getLiquidityInfo = async (
  chainId,
  poolAddress,
  accountAddress,
  bTokenSymbol,
) => {
  return catchApiError(
    async () => {
      [chainId, poolAddress, accountAddress] = checkApiInput(
        chainId,
        poolAddress,
        accountAddress
      );
      const api = poolApiFactory(chainId, poolAddress);
      await api.init();
      bTokenSymbol = checkToken(bTokenSymbol);
      return await api.getLiquidityInfo(accountAddress, bTokenSymbol);
    },
    [],
    {
      poolLiquidity: '',
      shares: '',
      maxRemovableShares: '',
      pnl: '',
      bToken0Symbol: '',
      swappedPnl: '',
    }
  );
};

