export const getEstimatedTimePrice = async (
  chainId,
  poolAddress,
  newNetVolume,
  symbol
) => {
  return catchApiError(
    async () => {
      [chainId, poolAddress] = checkApiInputWithoutAccount(
        chainId,
        poolAddress
      );
      symbol = checkToken(symbol);
      const api = poolApiFactory(chainId, poolAddress);
      await api.init();
      const pool = api.pool;
      const symbolInfo = getSymbolInfo(symbol, pool.symbols)
      const cost = calculateDpmmCost(
        symbolInfo.category === 'option'
          ? symbolInfo.theoreticalPrice
          : symbolInfo.indexPrice,
        symbolInfo.K,
        symbolInfo.netVolume,
        bg(newNetVolume)
      );
      return ''
      // return bg(cost).div(newNetVolume).toString();
    },
    [],
    ''
  );
};
