        category: s.category,
        K: s.K,
        cumulativeFundingPerVolume: s.cumulativeFundingPerVolume,
        curCumulativeFundingPerVolume: s.curCumulativeFundingPerVolume,
        curIndexPrice: s.curIndexPrice,
