  async getLiquidityUsed() {
    const pool = this.pool;
    return pool.symbols
      .reduce(
        (acc, s) =>
          acc.plus(
            bg(s.netVolume).abs().times(s.indexPrice).times(s.initialMarginRatio)
          ),
        bg(0)
      )
      .div(pool.state.liquidity)
      .toString();
  }

